.tabs {
    display: flex;
    gap: 24px; /* 調整每個 <span> 之間的間距 */
}
  
.tab {
    padding: 3px 5px;
    cursor: pointer;
    position: relative;
}
  
.tab::after {
  content: '';
  display: block;
  width: 80%; /* 底線長度 */
  height: 3px;
  background-color: red;
  position: absolute;
  bottom: 0;
  left: 12.5%; /* 將底線居中 */
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.tab.active::after {
  transform: scaleX(1); /* 當選中時展開底線 */
}
  